.CreateButton {
  float: right;
  margin-bottom: 0.5rem;
  min-width: 100px;
  width: fit-content;
}

.Button {
  float: right;
  margin-bottom: 0.5rem;
  min-width: 100px;
  width: fit-content;
}

.PropertyCard {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  cursor: pointer;
  padding: 0.5rem;
  width: 100%;
}

.PropertyDescription {
  flex-direction: column;
}

.PropertyDiv {
  margin-bottom: 1rem;
  margin-top: 1rem;
}

.ModalTitle {
  font-family: 'Gotham', sans-serif;
  font-size: 18px;
  font-weight: 700;
}

.CheckBox {
  display: flex;

  label {
    align-self: center;
    justify-self: center;
  }
}

.PropertyImage {
  border-radius: 16px;
  height: auto;
  max-height: 100%;
  max-width: 100%;
  width: auto;
}

.ButtonsDiv {
  display: flex;
  flex-direction: row !important;
}

.ButtonDiv {
  flex-direction: row !important;
  margin-right: 1rem;
}
